<template>
  <div class="play-list">
    <div class="music-container">
      <!-- 类型行 -->
      <div class="tab">
        <span :class="['item',type==index? 'active': '']" v-for="(item, index) in tab" :key="index" @click="getPlayList(index)">{{item}}</span>
      </div>
      <!-- 主体内容 -->
      <div class="playlist-content">
        <play-list-item
          class="item"
          v-for="(item, index) in playLists"
          :key="index"
          :playListId="item.id"
          :picUrl="item.coverImgUrl"
          :name="item.name"
          :playCount="item.playCount"
          @playlistDetail="goplaylistDetail(item.id)"
        />
      </div>
      <!-- 分页器 -->
      <div class="pagination" v-if="paginationShow">
        <el-pagination :background="true" layout="total, prev, pager, next ,jumper" :page-size="playListNum" :total="total" @current-change="handleCurrentChange"></el-pagination>
      </div>
    </div>
  </div>
</template>
<script>
// 工具包
import { req_update_playList_Tab } from "../network/song/index.js";
import tools from "../tools/formatData.js";
// 组件包
import playListItem from "../components/item/playListItem";
export default {
  name: "PlayList",
  components: {
    playListItem,
  },
  data() {
    return {
      // 当前歌单类型 （对应 tab 数组下标）
      type: 0,
      // 歌单类型
      tab: [
        "全部",
        "华语",
        "流行",
        "摇滚",
        "民谣",
        "电子",
        "另类/独立",
        "轻音乐",
        "综艺",
        "影视原声",
        "ACG",
      ],
      // 歌单
      playLists: [],
      // 歌单显示数量
      playListNum: 50,
      // 当前页数
      playListPage: 0,
      // 该类型歌单总数量
      total: 0,
      // 分页器
      paginationShow: false,
    };
  },
  watch: {
    playLists: function() {
      this.playLists.forEach((e, index, arr) => {
        let value = tools._tranNumber(e.playCount, 2);
        arr[index].playCount = value;
      });
      // 显示分页器
      this.paginationShow = true;
    },
  },
  created() {
    this.getPlayList();
  },
  methods: {
    // 获取歌单
    getPlayList(type = this.type) {
      this.type = type;
      document.documentElement.scrollTop = 0;

      // 计算跳过多少数量
      let offset;
      if (this.playListPage == 0) {
        // 排除掉页面第一次请求（页面初始化）
        offset = 0;
      } else {
        offset = this.playListNum * (this.playListPage - 1);
      }
     
      req_update_playList_Tab(this, {
         limit: 50,
         cat: this.tab[this.type],
         offset,
      }).then(res => {
          this.total = res.total;
          this.playLists = res.playlists;
          this.playListPage++;
      }).catch(error => {
          console.log(error);
      });
    },
    // 监听 页码值 改变的事件
    handleCurrentChange(newPage) {
      //把最新的页码（newPage）赋值给 动态的 commentPage
      this.playListPage = newPage;
      //获取到最新显示的页码值  重新发送axios请求
      this.getPlayList();
    },
    // 跳转到歌单详情
    goplaylistDetail(id) {
      this.$router.push({
        path: `/playListDetail/${new Date().getTime()}`,
        query: { id },
      });
    },
  },
};
</script>
<style lang="scss">
@import "../assets/scss/music.scss";
.play-list {
  .music-container {
    .tab {
      @include flex(flex-start);
      margin: 12px 0px;
      padding-bottom: 12px;
      border-bottom: 1px solid rgba($color: #000000, $alpha: 0.4);
      .item {
        display: block;
        padding: 10px, 12px;
        margin-right: 12px;
        text-align: center;
        @include font(16px, 20px, $colorB);
        &:hover {
          color: $colorA;
          cursor: pointer;
        }
      }
      .active {
        color: $colorA;
      }
    }
    .playlist-content {
      @include flex();
      flex-wrap: wrap;
    }
    .pagination {
      @include flex(center);
      margin-top: 20px;
      // 修改分页器的颜色
      .el-pagination.is-background .el-pager li:not(.disabled).active {
        background-color: #31c27c;
        color: #fff;
      }
    }
  }
}
</style>